  @mixin 함수() {
    background : #eeeeee;
    padding : 15px;
    border-radius : 5px;
    max-width : 500px;
    width : 100%;
    margin : auto;
  }
  .my-alert {
    @include 함수()
  }
  
  .my-alert2 {
    @extend .my-alert;
    background : yellow;
  }

  .my-alert p {
    margin-bottom : 0;
  }


  
  .wow-enter {
    opacity : 0
  }
  
  .wow-enter-active {
    opacity : 1;
    transition : all 500ms;
  }

